import Input from '../components/UI/Input';
import classes from './RegisterPage.module.css';
import { useState, Fragment, useEffect } from "react";
import Toggler from '../components/UI/Toggler';
import Button from '../components/UI/Button';
import AddGente from "../components/eventos/AddGente";
import useUser from '../hooks/use-user';
import AdderForm from '../components/eventos/AdderForm';
import Overlay from "../components/UI/Overlay";
import Loader from '../components/UI/Loader';
import { DayPicker } from 'react-day-picker';
import 'react-day-picker/dist/style.css';
import es from 'date-fns/locale/es';
import { collection, doc, updateDoc, getFirestore, getDoc } from "firebase/firestore"; 
import MyFirebase from "../database/firebase";
import { useNavigate, useParams } from 'react-router-dom';
import {GiSasquatch } from 'react-icons/gi'
const app = MyFirebase();

const EditarEventoPage = props => {
    const user = useUser(localStorage.getItem('uid'));
    const [addingDates, setAddingDates] = useState(false);
    const [addingGente, setAddingGente] = useState(false);
    const [addingSitio, setAddingSitio] = useState(false);
    const [addingRegalo, setAddingRegalo] = useState(false);
    const [participantesList, setParticipantesList] = useState([]);
    const [sitiosList, setSitiosList] = useState([]);
    const [regalosList, setRegalosList] = useState([]);
    const [days, setDays] = useState([]);
    const [enteredCat, setEnteredCat] = useState('');
    const [enteredCatId, setEnteredCatId] = useState('');
    const [enteredDesc, setEnteredDesc] = useState('');
    const [enteredTitle, setEnteredTitle] = useState('');
    const [isUploading, setIsUploading] = useState(false);
    const [loadedEvento, setLoadedEvento] = useState();
    const [isLoading, setIsloading] = useState(true);
    const [dateToVote, setDateToVote] = useState(false);
    const [sitioToVote, setSitioToVote] = useState(false);
    const [regaloToVote, setRegaloToVote] = useState(false);
    const db = getFirestore(app);
    const navigate = useNavigate();
    const params = useParams();
    const eventoId = params.eventoId;
    const getEvento = async ()=> {
        const eventoRef = doc(db, "fullEventos", eventoId);
        getDoc(eventoRef).then((docSnap) => {
            setLoadedEvento(docSnap.data())
            setParticipantesList(docSnap.data().users);
            setSitiosList(docSnap.data().availablePlaces);
            setRegalosList(docSnap.data().availableRegalos);
            setDays(docSnap.data().availableDates);
            setEnteredCat(docSnap.data().categoria);
            setEnteredCatId(docSnap.data().catId);
            setEnteredTitle(docSnap.data().title);
            setEnteredDesc(docSnap.data().desc);
            setDateToVote(docSnap.data().hasDates);
            setSitioToVote(docSnap.data().hasPlaces);
            setRegaloToVote(docSnap.data().hasRegalos);
            setIsloading(false)
        });
    }

    useEffect(() => {
        getEvento();
    }, [eventoId])
   
    const titleHandler = (event) => {
        setEnteredTitle(event.target.value);
    }
   
    const descHandler = (event) => {
        setEnteredDesc(event.target.value);
    }
    
    const catHandler = (event) => {
        setEnteredCatId(event.target.options[event.target.selectedIndex].value);
        setEnteredCat(event.target.options[event.target.selectedIndex].text)
    }


    const dateTogglingHandler = (isActive) => {
        setDateToVote(isActive)
    }

    const sitioTogglingHandler = (isActive) => {
        setSitioToVote(isActive)
    }

    const regalosTogglingHandler = (isActive) => {
        setRegaloToVote(isActive)
    }

    const addPeopleHandler = (nombre, toAdd) => {
        if (toAdd) {
            const participantesListCopy = [...participantesList];
            const updatedList = [...participantesListCopy, nombre]
            setParticipantesList(updatedList)
        } else {
            const participantesListCopy = [...participantesList];
            const updatedList = participantesListCopy.filter((participante) => participante !== nombre)
            setParticipantesList(updatedList)
        }
    }

    const addSitiosHandler = sitios => {
        const sitiosListCopy = [...sitiosList];
        const updatedList = [...sitiosListCopy, ...sitios]
        setSitiosList(updatedList);
        setAddingSitio(false);
    }

    const addRegalosHandler = regalos => {
        const regalosListCopy = [...regalosList];
        const updatedList = [...regalosListCopy, ...regalos]
        setRegalosList(updatedList);
        setAddingRegalo(false);
    }

    const submitHandler = (event) => {
        event.preventDefault();
        const eventosRef = collection(db, 'fullEventos');
        const teaserRef = collection(db, 'eventos')
        const newEvento = {
            eventoId: eventoId,
            title: enteredTitle,
            desc: enteredDesc,
            categoria: enteredCat,
            catId: enteredCatId,
            creator: {displayName: user.displayName, userId: localStorage.getItem('uid')},
            hasDates: dateToVote,
            hasRegalos: regaloToVote,
            hasPlaces: sitioToVote,
            users: participantesList,
            datesVotes: loadedEvento.datesVotes,
            regalosVotes: loadedEvento.regalosVotes,
            placesVotes: loadedEvento.placesVotes,
            availableDates: days,
            availableRegalos: regalosList,
            availablePlaces: sitiosList
        }

        const teaser = {
            eventoId: eventoId,
            title: enteredTitle,
            categoria: enteredCat,
            catId: enteredCatId,
            users: participantesList,
            creator: {displayName: user.displayName, userId: localStorage.getItem('uid')},
        }

        updateDoc(doc(eventosRef, eventoId), newEvento);
        updateDoc(doc(teaserRef, eventoId), teaser);

        setIsUploading(true);
        setTimeout(() => {
            navigate(`/evento/${eventoId}`)
        }, 2000)
    }


    const fechasDoneHandler= () => {
        setAddingDates(false)
        console.log(days)
    }

     
    const footer =
        days && days.length > 0
        ? `Has elegido ${days.length} día${(days.length !== 1) ? 's' : ''}.`
        : `Elige las fechas.`;

    const CalendarWrapper = () => {
        return (
            <Fragment>
                <Overlay onclick={() => setAddingDates(false)}/>
                <div className="calendar-wrapper">
                    <DayPicker
                    locale={es} 
                    mode="multiple"
                    selected={days}
                    onSelect={setDays}
                    footer={footer}   
                    />
                    <div className="evento-fechas-action">
                        <Button title="HECHO" onclick={fechasDoneHandler}/>
                    </div>
                </div>
            </Fragment>
        )
    }

    if (isUploading) {
        return <div className="uploading padding gapper center"><div className='sasquatch'><GiSasquatch /><GiSasquatch /><GiSasquatch /></div><h1>CREANDO EVENTO</h1></div>
    }

    if (isLoading) {
        return <div className="loader-wrapper"><Loader /></div>
    }

    return (
        <section className="gapper padding crear-evento-page center">
            <h1>CREAR NUEVO EVENTO</h1>
            <div className='marger'>
                <div className={classes.Form}>
                    <Input value={enteredTitle} onInputChange={titleHandler} name='title' type='text' label={'Nombre del Evento'}/>
                    <select onChange={catHandler} className='form-select' name='categoria'>
                        <option value={enteredCatId}>{enteredCat}</option>
                        <option value="CA">Cumpleaños</option>
                        <option value="ER">Escapada rural</option>
                        <option value="DP">Techno party</option>
                        <option value="VS">Viaje de ski</option>
                        <option value="EC">Encerrona</option>
                        <option value="VV">Viaje de verano</option>
                        <option value="GC">Grande celebraciones</option>
                    </select>
                    <Input value={enteredDesc} onInputChange={descHandler} name='desc' type='text' label={'Descripción del Evento'}/>
                    <Toggler initial={dateToVote} onActivated={dateTogglingHandler} label="¿Hay que elegir fechas?" />
                    {dateToVote && <div className='sitio-button gapper'><Button title="EDITAR FECHAS" onclick={() => setAddingDates(true)}/></div>}
                    {addingDates && <CalendarWrapper />}
                    <Toggler initial={sitioToVote} onActivated={sitioTogglingHandler} label="¿Hay que elegir sitios?" />
                    {sitioToVote && <div className='sitio-button gapper'><Button title="EDITAR SITIOS" onclick={() => setAddingSitio(true)}/></div>}
                    {addingSitio && <AdderForm onAdd={addSitiosHandler} isAdding="sitios" onclose={() => setAddingSitio(false)}/>}
                    <Toggler initial={regaloToVote} onActivated={regalosTogglingHandler} label="¿Hay que elegir regalos?" />
                    {regaloToVote && <div className='sitio-button gapper'><Button title="EDITAR REGALOS" onclick={() => setAddingRegalo(true)}/></div>}
                    {addingRegalo && <AdderForm onAdd={addRegalosHandler} isAdding="regalos" onclose={() => setAddingRegalo(false)}/>}
                    {addingGente && <AddGente onAdd={addPeopleHandler} isCreation={true} evento={{users:[]}} onclose={() => setAddingGente(false)}/>}
                    <div className='submit-wrapper gapper'>
                        <Button title="EDITAR PARTICIPANTES" onclick={() => setAddingGente(true)}/>
                        <Button title="¡TODO LISTO!" onclick={submitHandler}/>
                        <div className='cancel-wrapper'> <Button title="CANCELAR" onclick={() => navigate(-1)}/></div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default EditarEventoPage;